@import url("https://fonts.googleapis.com/css2?family=Comic+Neue:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&family=Luckiest+Guy&display=swap");

body {
  background-color: #0c0c0c;
  /* background: url(../../public/images/bg.webp); */
  background-size: 100%;
  height: 300vh;
  min-height: 100%;
  width: 100vw;
  margin: 0;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  font-family: "Comic Neue", cursive;
}

.content {
  color: white;
}

/* Header Style */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.head_wrapper {
  z-index: 10;
  display: flex;
  flex-direction: column;
  /* position: sticky; */
  top: 0;
}

.head {
  /* background: linear-gradient(95deg, #5d0e41, #a0153e); */
  background-color: #0c0c0c;
  display: flex;
  justify-content: space-between;
  color: white;
  padding: 1rem;
  align-items: center;
  width: 100vw;
}

#head_logo {
  margin-left: 20px;
  width: 60px;
  cursor: pointer;
  -webkit-user-drag: none;
  transition: 0.5s;
}

#head_logo:hover {
  transition: 0.5s;
  transform: rotate(45deg);
  filter: drop-shadow(0 0 50px #ff0);
}

.search-bar {
  background: url(../images/search.png);
  background-repeat: no-repeat;
  background-position: 97%;
  height: 45px;
  width: 400px;
  border-radius: 22.5px;
  padding-left: 20px;
  background-color: #2c2b44;
  border: none;
  color: white;
  font-size: 16px;
  padding-right: 50px;
  outline: none;
}

input:focus {
  transition: outline-color 0.6s;
  color: white;
  outline-style: solid;
  outline-width: 1px;
  outline-color: #ff0;
}

input::placeholder {
  color: #b7b7b7;
}

.head_botoes {
  margin-right: 20px;
}

svg {
  color: #fff;
}

.head_icon:hover {
  transition: background-color 0.2s;
  background-color: #b7b7b75e;
  border-radius: 10px;
}

.head_icon {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  width: 50px;
  height: 50px;
}

@keyframes tape {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 100% 0;
  }
}

/* Carousel Style */

#car_wrapper {
  display: flex;
  justify-content: center;
  /* gap: 1rem; */
  background-color: #0c0c0c;
  height: fit-content;
  padding: 0 50px;
  /* padding-bottom: 30px; */
  user-select: none;
  width: 80vw;
}

#car_container {
  display: flex;
  flex-direction: column;
  width: 100vw;
  align-items: center;
}

.car_XLsqr {
  /* border: solid black 1px; */
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  height: 100%;
  flex: 2.5;
}

.car_XLsqr > span {
  display: block;
  height: 492px;
  /* width: 100%; */
}

.car_Links {
  margin: 20px 0;
  right: auto;
  left: auto;
  position: absolute;
  display: block;
  background-color: black;
  opacity: 0.01;
  width: 90%;
  height: 90%;
  z-index: 10;
  -webkit-user-drag: none;
}

.car_Ssqrs {
  display: flex;
  flex-direction: column;
  flex: 1;
  margin: 35px 0;
}

.car_Ssqr {
  flex: 1;
  height: 50%;
  margin: 0 -30px;
  position: relative;
}

.car_bannerLinkS {
  top: 0;
  position: absolute;
  display: block;
  background-color: black;
  opacity: 0.01;
  width: 314px;
  height: 200px;
  z-index: 10;
  -webkit-user-drag: none;
}

/* Footer Style */

#foo_wrapper {
  /* position: fixed; */
  bottom: 0;
  color: white;
  background-color: #0c0c0c;
  display: flex;
  flex-direction: column;
  flex: 1;
  gap: 1rem;
  z-index: 10;
  /* height: 100px; */
  /* padding-bottom: 20px; */
}

.foo_content {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  width: 100vw;
  padding: 32px 65px;
}

.foo_logo {
  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 20px;
  width: 140px;
  height: 40px;
  background-color: yellow;
  color: black;
}

.foo_left {
  max-width: 400px;
  font-size: 16px;
}
.foo_right {
  gap: 1rem;
  align-items: flex-end;
  text-align: end;
  max-width: 400px;
}

.foo_right > h3 {
  color: yellow;
}

p > span {
  font-weight: bold;
  color: yellow;
}

h3:visited {
  text-decoration: none;
}

/* Estilo Tape */

.tape {
  height: 20px;
  width: 100%;
  background-size: 20%;
  background-image: url(../images/tape.png);
  animation: tape 20s linear infinite;
}

/* Estilo Home */

.page_content {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: white;
  /* background-color: #0c0c0c; */
  text-align: center;
}

.home_row {
  display: flex;
  gap: 1rem;
}

/*css pag do mangá*/
body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  /* background-color: #1c1c1c; Cor de fundo mais escura */
}
p {
  user-select: none;
}
.background {
  position: relative;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.blur-layer {
  background-image: url("https://cdn.marvel.com/u/prod/marvel/i/mg/f/60/5beda67b7e1c5/clean.jpg");
  background-size: cover;
  background-position: center;
  filter: blur(13px); /* Adiciona o desfoque à imagem de fundo */
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

.content-layer {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: rgb(0, 0, 0);
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.issue-title {
  margin-top: 10px;
}

.content {
  display: flex;
  justify-content: space-between;
  width: 80%;
  margin-top: 20px;
}

.issue {
  display: flex;
  align-items: center;
}

.issue-cover {
  width: 300px; /* Aumenta a imagem do quadrinho */
  height: 450px;
  margin-right: 20px;
  border-radius: 20px; /* Arredonda a imagem do quadrinho */
}

.info-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.info-box {
  background: #2c2b44; /* Fundo mais escuro */
  color: white; /* Texto branco para contraste */
  padding: 20px;
  margin: 10px;
  border-radius: 40px;
  border: 2px solid yellow; /* Margem amarela */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  width: 300px;
}

.volumes-box {
  background: #2c2b44; /* Fundo mais escuro */
  color: white; /* Texto branco para contraste */
  padding: 20px;
  margin: 10px;
  border-radius: 40px;
  border: 2px solid yellow; /* Margem amarela */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  width: 300px;
  height: 570px; /* Aumenta a altura da caixa dos volumes */
  position: absolute;
  top: 300px;
  right: -50px;
  transform: translateY(-50%);
}

.volumes-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 100%;
}

.volume {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.volume-cover {
  width: 80px;
  height: 120px;
  border-radius: 10px;
}

.synopsis-box {
  background: linear-gradient(
    to bottom,
    #ffeb3b,
    #fbc02d
  ); /* Fundo amarelo com gradiente */
  color: black; /* Texto preto para contraste */
  padding: 20px;
  margin: 10px;
  border-radius: 40px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); /* Sombra para efeito 3D */
  width: 80%; /* Estende a caixa de sinopse */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.authors-box {
  background: #2c2b44; /* Fundo mais escuro */
  color: rgb(255, 255, 255); /* Texto branco para contraste */
  padding: 20px;
  margin: 10px;
  border-radius: 40px;
  border: 2px solid yellow; /* Margem amarela */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  width: 80%; /* Estende a caixa de autores */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.authors-container {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.author {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
  text-align: center;
}

.author img {
  width: 90px;
  height: 90px;
  border-radius: 50%;
}

.main-content {
  padding: 20px;
}

.comic-title {
  text-align: center;
  margin-bottom: 70px;
}

.layout-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 20px;
  width: 100vw;
  padding: 0 6rem;
}

.comic-image img {
  width: 300px;
  height: auto;
  border-radius: 10px;
}

.info-author-container {
  display: flex;
  flex-direction: column;
  gap: 150px; /* altura da box de autores */
  width: 100%;
}

.info-box {
  background: #2c2b44; /* Fundo mais escuro */
  color: rgb(255, 255, 255); /* Texto branco para contraste */
  padding: 20px;
  border-radius: 40px;
  border: 2px solid yellow; /* Margem amarela */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  width: 40%; /* Estende a caixa de autores */
  display: flex;
  flex-direction: column;
  border-radius: 10px;
}

.authors-box {
  background: #2c2b44; /* Fundo mais escuro */
  color: rgb(255, 255, 255); /* Texto branco para contraste */
  margin: 10px;
  border-radius: 40px;
  border: 2px solid yellow; /* Margem amarela */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  width: 60%;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border-radius: 10px;
}

.authors-container {
  display: flex;
  gap: 5px;
}

.author img {
  width: 100px;
  height: 100px;
  border: 2px solid yellow; /* Margem amarela */
  border-radius: 50%;
}

.collections-box {
  color: white;
  width: 650px;
  background-color: #2c2b44;
  border: 2px solid yellow; /* Margem amarela */
  border-radius: 10px;
  max-height: 470px;
  overflow-y: auto;
}

.collections-title {
  padding: 10px;
  display: flex;
  justify-content: center;
}

.collections-scroll {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.collection-item {
  padding: 20px;
  display: flex;
  align-items: center;
}

.collection-item img {
  width: 90px;
  height: auto;
  border-radius: 10px;
}

.collection-item p {
  color: white;
  text-align: center;
  margin-top: 5px;
  font-size: 14px;
}

/* Estiliza a barra de rolagem no Webkit*/
.collections-box::-webkit-scrollbar {
  width: 8px; /* Largura da barra de rolagem */
}

.collections-box::-webkit-scrollbar-track {
  background: #f0f0f0; /* Cor de fundo da barra de rolagem */
}

.collections-box::-webkit-scrollbar-thumb {
  background-color: yellow; /* Cor da parte rolável */
  border-radius: 10px; /* Bordas arredondadas da barra */
}

.collections-box::-webkit-scrollbar-thumb:hover {
  background: #555; /* Cor ao passar o mouse */
}

/* CSS da Pagina de Login */
.login-page {
  display: flex;
  justify-content: center;
  padding: 30px;
  height: 100vh;
}

#wrapper-login {
  width: 90vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  color: white;
  background-color: #000000;
  border-radius: 30px;
}

.left-side {
  color: #000000;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  flex: 2;
  justify-content: space-around;
  align-items: center;
  background-color: #f3f386;
  border-radius: 30px 0 0 30px;
}

.cadastro {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: black;
  gap: 1rem;
}

.cadastroLink {
  color: white;
  background-color: black;
  font-size: 32px;
  /* border: solid */
  border-radius: 12px;
  padding: 10px;
}

.right-side {
  display: flex;
  height: 100%;
  width: 100%;
  flex: 3;
  justify-content: center;
  align-items: center;
  /* background-color: #fbc02d; */
}

.form-login {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title-login {
  font-family: "Segoe UI", Tahoma, Verdana, sans-serif;
}

.text-bar-login {
  background-color: #2c2b44;
  border-radius: 10px;
  height: 50px;
  width: 60%;
  font-size: 20px;
  color: white;
  outline: none;
  border: none;
  padding: 10px;
  margin: 15px;
}

#login-button {
  margin: 15px 0;
  padding: 10px;
  border-radius: 10px;
  width: 60%;
  height: 12%;
  border: none;
  background-color: #f3f386;
  cursor: pointer;
}

