/* css editora marvel */

.editorapage {
  position: relative;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: url("https://opinioescertificadas.com.br/wp-content/uploads/2020/10/melhores-quadrinhos-da-marvel.jpg")
    no-repeat center center fixed;
  background-size: cover;
  overflow: hidden;
  padding: 20px;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7); /* Fundo escurecido */
  z-index: 0;
}

.editor-logo {
  max-width: 300px; /* Tamanho da logo */
  margin-bottom: 40px;
  z-index: 1; /* Para estar acima da overlay */
}

.bio {
  background: rgba(255, 255, 255, 0.3); /* Balão para a bio */
  padding: 15px;
  border-radius: 10px;
  margin: 20px auto; /* Centraliza o balão */
  z-index: 1; /* Para estar acima da overlay */
  text-align: center; /* Centraliza o texto */
  max-width: 1300px; /* Limita a largura do balão */
}

.creators {
  z-index: 1; /* Para estar acima da overlay */
  text-align: center;
  color: white; /* Cor da fonte dos criadores */
  font-family: Arial, sans-serif; /* Fonte Arial */
}

.creator-images {
  display: flex;
  justify-content: center;
  gap: 20px; /* Espaço entre as imagens */
  margin-top: 10px;
}

.creator-img {
  width: 120px; /* Tamanho das imagens dos autores */
  height: 120px;
  border-radius: 50%; /* Imagens arredondadas */
  border: 2px solid white; /* Borda branca */
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.5); /* Sombra leve */
}

